import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import UXSVG from "./UXSVG";
import style from "../styles.css";
import MainContentService from "../mainContentService/mainContentService";

export default class UX extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configuro landscape --> */}
        <div id="lying" />

        {/* <!-- Configuro natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside usabilidade">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>UX</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dgitozero</Link>
                  </li>
                  <li>
                    <Link to="/design">Design</Link>
                  </li>
                  <li>
                    <span>UX</span>
                  </li>
                </Breadcrumb>

                <MainContentService
                  service="ux"
                  svg={UXSVG}
                  mainTitle="A experincia do usurio"
                  subTitle="Conjunto de aspectos referentes  interao do usurio"
                  firstText="A grande maioria das pessoas tende a desistir rapidamente se percebem que um site no  bem como elas esperavam. Estamos acostumados com a agilidade e facilidade, e cada vez menos temos pacincia para sites que no funcionam bem. Da toda a importncia dessa cincia moderna chamada User Experience. UX est relacionada ao design, que  responsvel por desenvolver a interface de forma que favorea a usabilidade e mxima facilidade de entendimento do usurio enquanto navega."
                  secondText="O algoritmo do Google est sempre evoluindo e j consegue reconhecer muitos padres de UX, valorizando quem os segue e penalizando aqueles que no investem em usabilidade. O resultado disso impacta diretamente no SEO. A dgitozero conta com um time de design com bastante experincia em projetos de usabilidade e antenado nas melhores prticas adotadas pelo mercado atualmente."
                />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



