import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import MainContentService from "../mainContentService/mainContentService";
import SiteSVG from "./SiteSVG";

export default class Site extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configuro landscape --> */}
        <div id="lying" />

        {/* <!-- Configuro natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside site">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Criao de Sites</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dgitozero</Link>
                  </li>
                  <li>
                    <Link to="/design">Design</Link>
                  </li>
                  <li>
                    <span>Site</span>
                  </li>
                </Breadcrumb>

                <MainContentService
                  service="site"
                  svg={SiteSVG}
                  mainTitle="Mais que apenas sites"
                  subTitle="Websites otimizados para SEO"
                  firstText="Na dgitozero desenvolvemos websites modernos e customizados para todos os segmentos e portes de negcio. Nosso time tem a expertise necessria para desenvolver um site totalmente responsivo para mobile, tablet e tambm alinhados s melhores prticas do Google: o chamado SEO (Search Engine Optimization). Atravs de um bom trabalho de SEO, seu um site estar otimizado, assim sua empresa tende a ser posicionada entre as mais bem ranqueadas na primeira pgina de resultados de pesquisas do Google, atraindo a ateno e disputando cada clique do seu pblico-alvo com a concorrncia."
                  secondText="Entregamos pginas intuitivas, interativas e diretas, focadas na clareza e boa experincia do usurio, o chamado User Experience, resumido na sigla UX. Cada vez mais, o Google valoriza sites que no confundam o usurio, e sim forneam facilidade, sem exigir que ele tenha que pensar muito. O buscador penaliza pginas que no estejam adaptadas s boas prticas de UX e dificultem a vida de quem navega, por isso  muito importante investir em um bom website para sua empresa. Afinal de contas,  a cara do seu negcio."
                />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



